import React from 'react';
import {AdvantageBlock} from '../../../../common/Components/AdvantageBlock/AdvantageBlock';
import style from './Advantages.module.scss'
import {CmsDataType} from '../../ElonMaskCMS/cms-reducer';


export const Advantages: React.FC<CmsDataType> = (props) => {

    console.log(props)

    return (
        <div className={style.advantages}>
            <AdvantageBlock upText={''} middleText={props.marketPosition} downText={' '} />
            <AdvantageBlock upText={''} middleText={props.guaranty + '%'} downText={''} />
            <AdvantageBlock upText={'  '} middleText={props.year} downText={' '} year={'.'}/>
            <AdvantageBlock upText={''} middleText={props.travellingDuration} downText={''} />
        </div>
    );
};